import { h, Component } from 'preact';
import util from 'preact-util';
import { observer } from 'mobx-preact';
import { Text, Localizer } from 'preact-i18n';
import linkstate from 'linkstate';
import Markdown from 'preact-markdown';

import mu from '../../lib/musher-util';

import style from './style.css';

const MARGIN_TOP = mu.marginTop(true);
const MARGIN_BOTTOM = mu.marginBottom();

const initialState = {};

@observer
class TvFreetext extends Component {
    constructor(props) {
        super(props);
        this.state = {
            ...initialState,
        };
    }

    showText = () => {
        const { text } = this.state;
        this.setState({ showText: text });
    }

    render() {
        const { showText = '' } = this.state;
        const { goto, gotoTitle, routerName, position = 'right' } = this.props;
        return (<>
            <div class={`fixed-top ${style.hoverElement}`} style='height: 100px;'>
                <div class={`${style.hoverelementonly}`}>
                    <div class={`row`}>
                        <div class='col-4 offset-4 form-inline'>
                            <div class='form-group'>
                                <label class='text-success mr-2'>Enter text</label>
                                <textarea
                                    class='form-control form-control-lg text-success border-success'
                                    style='background-color: inherit;'
                                    type='text'
                                    rows='5'
                                    onInput={linkstate(this, 'text')}
                                />
                                <button
                                    type='button'
                                    class='btn btn-lg btn-outline-success'
                                    onClick={this.showText}
                                >Show Text</button>
                            </div>
                        </div>
                        <div class='col-1'>
                            <a
                                class={`btn btn-link text-nowrap ${position === 'left' ? 'float-right' : 'float-left'}`}
                                style='text-overflow: ellipsis; overflow: hidden;'
                                href={`/tv/freetext/${position === 'left' ? 'right' : 'left'}`}
                            >
                                {position === 'left' ? <>
                                    right <i class='fas fa-angle-right' />
                                </> : <>
                                    <i class='fas fa-angle-left' /> left
                                </>}
                            </a>
                        </div>
                    </div>
                </div>
            </div>
			<div class='container-fluid' style={`margin-bottom: ${MARGIN_BOTTOM}; margin-top: ${MARGIN_TOP}; height: calc(100% - ${MARGIN_BOTTOM} - ${MARGIN_TOP});`}>
                <div class='row'>
                    <div class={position === 'left' ? 'col-4 ml-5' : 'col-4 offset-8 mr-5'}>
                        <div
                            class='px-4 py-2 mr-5 overflow-hidden'
                            style='
                                background-color: #FB4114; 
                                color: #ffffff; 
                                border-radius: 10px 10px 0 0; 
                                font-size: 1.8em;
                            '
                        />
                        <div
                            class='text-white mr-5 p-4'
                            style='
                                font-size: 2.0em;
                                background-color: #1C1F4C;
                                background-image: url("https://themusher.app/assets/tv-single-background.jpg");
                                background-size: cover !important;
                                background-position: 0 0;    
                            '
                        >
                            {showText && <>
                                <Markdown markdown={showText} markdownOpts={mu.getMarkdownOptions()} />
                            </>}
                        </div>
                        <div
                            class='px-4 py-2 mr-5 overflow-hidden'
                            style='
                                background-color: #FB4114; 
                                color: #ffffff; 
                                border-radius: 0 0 10px 10px; 
                                font-size: 1.8em;
                            '
                        />
                    </div>
                </div>
            </div>
        </>);
    }
}

export default TvFreetext;
